"use strict";

/* global $, Helper */
$(function () {
  var form = $('form');
  /**
   * Gets files names
   *
   * @param {object} files - The file list.
   *
   * @return {array}
   */

  function getFilesNames(files) {
    return Object.keys(files).map(function (key) {
      return files[key].name;
    });
  }
  /**
   * Removes an input with a label.
   *
   * @param {array} filesNamesWrappers
   *
   * @return {void}
   */


  function removeFileNames(filesNamesWrappers) {
    filesNamesWrappers.forEach(function (item) {
      item.remove();
    });
  }
  /**
   * Clones an input with a label.
   *
   * @param {object} customFileInput - The jQuery element.
   * @param {object} customFileLabel - The jQuery element.
   * @param {object} customFile - The jQuery element.
   *
   * @return {void}
   */


  function cloneInput(customFileInput, customFileLabel, customFile) {
    var customFileInputId = customFileInput.attr('id');
    var uniqueId = Helper.getUniqueId();
    var newCustomFileInputId = "".concat(customFileInputId.split('_')[0], "_").concat(uniqueId);
    customFileInput.clone().val(null).removeClass('input_error').attr('id', newCustomFileInputId).prependTo(customFile);
    customFileLabel.clone().removeClass('input_error').attr('for', newCustomFileInputId).prependTo(customFile);
    $("#".concat(customFileInputId, "-error")).remove();
    customFileLabel.hide();
  }
  /**
   * Adds the file names.
   *
   * @param {array} filesNames - Files names.
   * @param {object} customFileInput - The jQuery element.
   * @param {object} customFileLabel - The jQuery element.
   * @param {object} customFile - The jQuery element.
   * @param {boolean} isClone - Is replace names.
   *
   * @return {void}
   */


  function addFileNames(filesNames, customFileInput, customFileLabel, customFile) {
    var isClone = arguments.length > 4 && arguments[4] !== undefined ? arguments[4] : true;
    var filesNamesContainer = customFile.children('.custom-file__files-names');

    if (!filesNamesContainer.length) {
      filesNamesContainer = $('<div>', {
        class: 'custom-file__files-names'
      }).appendTo(customFile);
    }

    if (!isClone) {
      filesNamesContainer.html('');
    }

    var filesNamesWrappers = [];

    for (var i = 0; i < filesNames.length; i += 1) {
      filesNamesWrappers.push($('<div>', {
        class: 'custom-file__file-name',
        text: filesNames[i]
      }));
    }

    filesNamesWrappers.forEach(function (item, i) {
      if (i === 0) {
        $('<button>', {
          class: 'custom-file__close-btn',
          html: '&#10006;'
        }).on('click', function () {
          removeFileNames(filesNamesWrappers);

          if (isClone) {
            customFileInput.remove();
            customFileLabel.remove();
          } else {
            customFileInput.val(null);
          }
        }).prependTo(item);
      }

      item.appendTo(filesNamesContainer);
    });
  }

  form.on('change', '.custom-file-input', function onChange() {
    var customFileInput = $(this);
    var customFileLabel = customFileInput.prev('.custom-file-label');
    var customFile = customFileInput.parent('.custom-file');
    var files = this.files;

    if (files.length > 0) {
      var filesNames = getFilesNames(files);
      var isMultiple = customFileInput.hasClass('custom-file-input__multiple');

      if (isMultiple) {
        cloneInput(customFileInput, customFileLabel, customFile);
      }

      addFileNames(filesNames, customFileInput, customFileLabel, customFile, isMultiple);
    } else {
      customFile.children('.custom-file__files-names').remove();
    }
  });
});