"use strict";

/* global $, Form */
$(function () {
  var phoneNumberForm = $('#js-formPhoneNumber');
  var phoneInput = $('#js-formPhoneNumberInputPhone');
  var submitButton = $('#js-formPhoneNumberBtnSubmit');
  phoneInput.inputmask('+7(999)999-99-99');
  phoneNumberForm.validate({
    highlight: function highlight(element) {
      $(element).addClass('input_error');
    },
    unhighlight: function unhighlight(element) {
      $(element).removeClass('input_error');
    },
    submitHandler: function submitHandler(form) {
      var action = $(form).attr('action');
      var data = new FormData(form);

      if (!submitButton.is(':disabled')) {
        Form.send(action, data, submitButton);
      }
    },
    rules: {
      phone: {
        required: true,
        phone_ru: true,
        normalizer: function normalizer(value) {
          return $.trim(value);
        }
      }
    },
    messages: {
      phone: {
        required: 'Это обязательное поле',
        phone_ru: 'Введите номер в формате +7(xxx)xxx-xx-xx'
      }
    },
    errorClass: 'error-text',
    errorElement: 'span'
  });
  phoneNumberForm.on('keyup blur change', 'input, select, textarea', function () {
    Form.changeButtonStatus(phoneNumberForm, submitButton);
  });
});