"use strict";

/* global $, Form, Toaster, Cookies */
$(function () {
  var timer = $('#js-codeSmsTimer');
  var timerEnd = timer.data('timer-end');
  var repeatLink = $('#js-codeSmsRepeatSend');
  var repeatText = $('#js-codeSmsRepeatText');
  timer.countdown(timerEnd * 1000).on('update.countdown', function onUpdate(event) {
    $(this).html(event.strftime('%M:%S'));
  }).on('finish.countdown', function onFinish(event) {
    $(this).html(event.strftime('00:00'));
    repeatLink.removeClass('link_disabled');
    repeatText.hide();
  });
  repeatLink.on('click', function onClick() {
    if ($(this).hasClass('link_disabled')) return;
    var link = $(this);
    $.ajax({
      cache: false,
      dataType: 'json',
      method: 'POST',
      url: link.data('link-action'),
      success: function success(data) {
        repeatLink.addClass('link_disabled');
        repeatText.show();
        Toaster.create(data.message.body, data.message.type);
        var newTimerEnd = Date.now() / 1000 + 180;
        Cookies.set('confirm_timer', newTimerEnd);
        timer.countdown(newTimerEnd * 1000).on('update.countdown', function onUpdate(event) {
          $(this).html(event.strftime('%M:%S'));
        }).on('finish.countdown', function onFinish(event) {
          $(this).html(event.strftime('00:00'));
          repeatLink.removeClass('link_disabled');
          repeatText.hide();
        });
      },
      error: function error() {
        Toaster.create('Произошла ошибка, попробуйте ещё раз.', 'error');
      }
    });
  });
});