"use strict";

/* global $, Form, Toaster, Cookies */
$(function () {
  var timer = $('#js-profileClientTimer');
  var timerEnable = timer.data('timer-enable');
  if (!timerEnable) return;
  var timerEnd = timer.data('timer-end');
  var progressBar = $('#js-profileClientProgressBar');
  var progressBarInner = progressBar.find('div');
  var progressBarEndAfter = progressBar.data('end-after');
  var modalSendRequest = $('#js-modalSendRequest');
  var modalNoResponse = $('#js-modalNoResponse');
  timer.countdown(timerEnd * 1000).on('update.countdown', function onUpdate(event) {
    $(this).html(event.strftime('%M:%S'));
    progressBarInner.width((progressBarEndAfter - event.offset.totalSeconds) * progressBar.width() / progressBarEndAfter);
  }).on('finish.countdown', function onFinish(event) {
    $(this).html(event.strftime('00:00'));
    $(this).parent().addClass('timer_flash');
    progressBarInner.width('100%');
    modalSendRequest.modal('hide');
    modalNoResponse.modal({
      backdrop: 'static',
      keyboard: false
    });
  });
});