"use strict";

/* global $ */
$(function () {
  if (!$('.body_home').length) {
    return;
  }

  var pageMenuHeight = parseInt($('.page__content').css('padding-top'), 10);
  $(window).resize(function () {
    pageMenuHeight = parseInt($('.page__content').css('padding-top'), 10);
  });

  function setPageHeight() {
    var page = $('.page');
    var sideMenuPage = $('#js-sideMenuPage');
    var curHeight = page.height();
    var newHeight = sideMenuPage.height() + parseInt(pageMenuHeight, 10);
    page.height(curHeight).animate({
      height: newHeight
    }, {
      duration: 200,
      queue: false
    });
  }

  function setPageHeightIfSideMenuOpen() {
    var page = $('.page');
    var sideMenuPage = $('#js-sideMenuPage');

    if (sideMenuPage.hasClass('open')) {
      setPageHeight();
    } else {
      page.css('height', 'auto');
    }
  }

  function scrollToContent() {
    $('html, body').animate({
      scrollTop: $('#js-sideMenuContent').offset().top - pageMenuHeight
    }, 500);
  }

  function removeActiveClass() {
    $('.dropdown-link').each(function (i, elem) {
      $(elem).removeClass('active');
    });
    $('.dropdown__item').each(function (i, elem) {
      $(elem).removeClass('active');
    });
  }

  function closeDropdowns() {
    $('#js-pageMenuToggle').removeClass('is-active');
    $('.dropdown-link').removeClass('open');
  }

  function findSecondMenuPoint(elem) {
    $(elem).addClass('active');
    $('.dropdown-link').each(function (index, element) {
      if ($(element).data('link') === $(elem).data('link')) {
        if ($(element).hasClass('dropdown-link_toggle-top') && $(window).width() <= 991 || $(element).hasClass('dropdown-link_toggle-bottom')) {
          var item = element.closest('.dropdown__item');
          var dropdown = $(item).find('.dropdown');
          $(dropdown).slideToggle({
            start: function start() {
              setPageHeight();
            },
            done: function done() {
              setPageHeight();
            }
          });
          $(element).toggleClass('open');

          if ($('#js-sideMenuPage').hasClass('open')) {
            setPageHeight();
          }
        }

        if ($(element).hasClass('dropdown-link_side')) {
          var _item = element.closest('.dropdown__item');

          var _dropdown = $(_item).find('.dropdown');

          $(_dropdown).slideDown(function () {
            setPageHeight();
          });
          $(_item).find('.dropdown-link_toggle').addClass('open');

          if ($('#js-sideMenuPage').hasClass('open')) {
            setPageHeight();
          }
        }

        $(element).addClass('active');
        $(element).parents('.dropdown__item').find('.dropdown__item-link').addClass('active');
        $(element).parents('.dropdown__item').addClass('active');
      }
    });
  }

  $('body').bind('click', function (e) {
    var arrowMobile = $('.arrow_mobile');
    var arrowDesktop = $('.arrow_desktop');
    var sideMenuPage = $('#js-sideMenuPage');
    $('.page').css('overflow', 'hidden');

    if (arrowMobile.has(e.target).length > 0 || arrowDesktop.has(e.target).length > 0) {
      arrowMobile.removeClass('is-active');
      arrowDesktop.removeClass('is-active');
      sideMenuPage.removeClass('open').addClass('close');
      setTimeout(function () {
        $('.page').css('height', 'auto');
      }, 300);
      closeDropdowns();
      removeActiveClass();
    } else {
      setTimeout(setPageHeight, 200);
      $('.side-menu .dropdown-link:not(.dropdown-link_no-click)').each(function (i, elem) {
        if (e.target === elem) {
          removeActiveClass();
          findSecondMenuPoint(elem);

          if ($(window).width() <= 991) {
            scrollToContent();
          }
        }
      });
      $('.page-menu .dropdown-link:not(.dropdown-link_no-click)').each(function (i, elem) {
        if (e.target === elem) {
          removeActiveClass();
          findSecondMenuPoint(elem);

          var closeMenu = function closeMenu() {
            $('#js-pageMenuToggle').removeClass('is-active');
            $('#js-pageMenu').slideUp('fast', function () {
              setPageHeightIfSideMenuOpen();
            });
          };

          if ($(window).width() <= 991) {
            closeMenu();
            scrollToContent();
          }

          if (!sideMenuPage.hasClass('open')) {
            $('.arrow_mobile').addClass('is-active');
            $('.arrow_desktop').addClass('is-active');
            sideMenuPage.removeClass('close').addClass('open');
            setPageHeight();
            $(window).resize(function () {
              setPageHeightIfSideMenuOpen();
            });
          }
        }
      });
    }
  });
  $(window).resize(function () {
    closeDropdowns();
    setPageHeightIfSideMenuOpen();
  });
});