"use strict";

/* global $, Form */
$(function () {
  var page = $('#js-page');
  var ajaxData = {};
  var ajaxTimeout = page.data('ajax-timeout');
  var ajaxAction = page.data('ajax-action');

  if (ajaxTimeout) {
    ajaxData.timeout = ajaxTimeout;
  }

  if (ajaxAction) {
    ajaxData.action = ajaxAction;
    Form.ajax(ajaxData);
  }
});